.todocntr{
    width: 90%;
    max-height: 95%;
    overflow: scroll;
    padding: 20px 0px;
}
.todocntr::-webkit-scrollbar{
    display: none;
}
td,th{
    text-align: center;
}
td svg{
    cursor: pointer;
    font-size: 2rem !important;
}
.icondelete svg{
    color: #fb7400;
}
.iconedit svg{
    color: #001fff;
}