.sidepanel-l,
.sidepanel-d{
    width: 15%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-right: solid 1px #ccc;
}
.sidepanel-d{
    background-color: #333;
}
.side-active{
    background-color: #1a73e8;
}
.first-element .f-ele{
    /* border: 1px solid red; */
    height: 48px;
    display: flex;
    align-items: center;
    margin: 5px 0px;
}
.first-element .f-ele .icondiv{
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
    /* background-color: #1a73e8; */
}
.first-element .f-ele .icondiv .f-icon-l,
.first-element .f-ele .icondiv .f-icon-d{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #666769;
}
.first-element .f-ele .icondiv .f-icon-d{
    color: rgb(248, 248, 248);
}
.first-element .f-ele .f-name-l,
.first-element .f-ele .f-name-d{
    flex: 1;
    height: 48px;
    display: flex;
    align-items: center;
    /* background-color: #1a73e8; */
    padding-left: 15px;
    font-size: 15px;
    font-weight: 500;
    color: #666769;
    font-family: 'Roboto', sans-serif;
}
.first-element .f-ele .f-name-d{
    color: #ffffff;
}
.first-element .f-ele .f-halfround{
    height: 48px;
    width: 48px;
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
    /* background-color: #1a73e8; */
}