nav{
    width: 100%;
    padding: 5px 15px;
    display: flex;
    height: 70px;
}
nav .all-nav{
  display: flex;
  flex: 1;
}

.navbar-theme-l,
.navbar-theme-d{
    background-color: #efefef;
    border-bottom: solid 1px #ccc;
}
.navbar-theme-d{
    background-color: #222;
    border-bottom: solid 1px #555;
}
.logo-section{
    display: flex;
    align-items: center;
    gap: 5px;
}
.logo-section img{
  width: 32px;
  height: 32px;
}
.logo-section .logo-analytics-l,
.logo-section .logo-analytics-d{
  color: #5f6368;
  font-family: 'Roboto', sans-serif;
  margin-right: 15px;
}
.logo-section .logo-analytics-d{
  color: #ffffff;
}
.nav-account-l,
.nav-account-d{
  margin-right: 5px;
  padding: 5px 15px;
  cursor: pointer;
  border-radius: 12px;
}
.nav-account-l:hover,
.nav-account-d:hover{
  background-color: rgba(95,99,104,.1);
}
.nav-account-d:hover{
  background-color: rgba(231, 233, 236, 0.185);
}
.nav-account-l span,
.nav-account-d span{
  color: #5f6368;
  font: 400 12px/16px 'Roboto',Helvetica,Arial,sans-serif; 
}
.nav-account-d span{
  color: #ffffff;
}
.nav-account-l h1,
.nav-account-d h1{
  color: #666769;
  max-width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 25px;
  font-family: 'Roboto', sans-serif;
}
.nav-account-d h1{
  color: #ffffff;
}

.nav-input-l,
.nav-input-d{
  width: 58%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-input-l input,
.nav-input-d input{
  width: 100%;
  height: 47px;
  border: none;
  padding-left: 30px;
  font-size: 18px;
  font-weight: 700;
  background-color: rgba(71, 71, 71, 0.144);
  border-radius: 8px;
}
.nav-input-d input{
  background-color: rgba(255, 255, 255, 0.377);
}
.nav-input-l input::placeholder,
.nav-input-d input::placeholder{
 color: #666769;
}
.nav-input-d input::placeholder{
  color: #ffffff;
}
.nav-input-l input:focus,
.nav-input-d input:focus{
  outline: none;
}

nav .last-container{
  width: 25%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 10px;
}
nav .last-container .nav-icon-bars-l,
nav .last-container .nav-icon-bars-d{
  height: 28px;
  width: 28px;
  font-size: 25px;
  text-align: center;
  color: #3c4043;
}
nav .last-container .nav-icon-bars-d{
  color: #ffffff;
}

/* -----------------Switcher-------------------- */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 28px;
  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
  /* -----------------closed switcher--------------------- */